import * as React from 'react'
import {NavLink} from 'react-router-dom'

interface INotFoundProps {
    linkToBack?: string,
    textToBack?: string,
    img?: string,
    text?: string
}

const goodText = ',        .  URL-     ,       .'
const goodTitle = '  '

const NotFound: React.FC<INotFoundProps> = ({
                                                linkToBack = '/',
                                                textToBack = '  ',
                                                text= `,    . . .
                                                    
                                                `,
                                                img= ''
                                            }) => {
    return (
        <>
            <div>
                <NavLink to={linkToBack}>
                    {textToBack}
                </NavLink>
                {text && <p>{text}</p>}
                {img && <img src={`${img}`} alt="sorry,men"/>}
            </div>
        </>
    );
}

export default NotFound;